<template>
  <div :id='adSlot' :style="adStyle"></div>
</template>

<script>
  export default {
    props: {
      adClient: {
        type: String,
        required: false
      },
      refresh: {
        type: Boolean,
        default: false
      },
      adSlot: {
        type: String,
        required: true
      },
      adFormat: {
        type: String,
        required: false,
        default: 'auto'
      },
      adStyle: {
        type: String,
        required: true
      },
      height: {
        type: Number,
        required: false
      },
      width: {
        type: Number,
        required: false
      }
    },
    methods: {
      pushAds (id) {
        var slot = this.adSlot
        googletag.cmd.push(() => {
          var target = googletag.defineSlot(id, [this.width, this.height], slot).addService(googletag.pubads())
          googletag.display(slot)
          this.$store.dispatch('setSlot', target)
        })
      }
    },
    mounted() {
      var slot = this.adSlot
      if (this.refresh) {
        if (this.height === 240 && this.width === 120) {
          this.pushAds('/306208525/ad-unit-three')
        } else if (this.height === 60 && this.width === 468) {
          this.pushAds('/306208525/horizontal-list-ads-468x60')
        } else if (this.height === 90 && this.width === 728) {
          this.pushAds('/306208525/horizontal-ads-728x90')
        } else if (this.height === 50 && this.width === 320) {
          this.pushAds('/306208525/horizontal-mobile-ads-320x50')
        }
      } else {
        googletag.cmd.push(function() { googletag.display(slot); });
      }
    }
  }
</script>



