import _ from 'lodash'

const state = {
  slot: []
}

const getters = {
  getSlot: state => state.slot,
}

const mutations = {
  setSlot (state, data) {
    state.slot.push(data)
  },
  clearSlot (state) {
    state.slot = []
  }
}

const actions = {
  setSlot ({commit}, data) {
    commit('setSlot', data)
  },
  clearSlot ({commit}) {
    commit('clearSlot')
  }
}

export default {
  state,
  getters,
  actions,
  mutations
}



