const state = {
  pageTitle: 'Best Bargains - The Best Daily Bargains and Offers',
  pageDescription: 'Find your daily best bargains here. Get the best price for bargains, offers, products from trusted sources such as Amazon, Ebay, Best Buy, Macys and many more. All bargains on our website are hand-picked by our staffs.'
}

const getters = {
  getPageTitle: state => state.pageTitle,
  getPageDescription: state => state.pageDescription
}

const mutations = {
  setTitle (state, data) {
    state.pageTitle = data
  },
  setDescription (state, data) {
    state.pageDescription = data
  },
  updateHeader () {
    this.$emit('updateHead')
    console.log('updated')
  }
}

const actions = {
  setTitle ({commit}, data) {
    commit('setTitle', data)
  },
  setDescription ({commit}, data) {
    commit('setDescription', data)
  },
  updateHeader ({commit}) {
    commit('updateHeader')
  }
}

export default {
  state,
  getters,
  actions,
  mutations
}



